#projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 50px;
  margin-right: 50px;
  margin-bottom: 200px;
}

#title {
  text-align: center;
}

#pageTitle {
  font-size: 40px;
  text-align: center;
}

#projectEntry {
  background-color: rgb(28, 28, 29);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.7);
  color: rgb(248, 242, 242);
  width: 100%;
  margin-bottom: 50px;
}

h2 {
  align-self: flex-start;
  padding-left: 25px;
}

#h2Title {
  text-align: center;
}

#images {
  display: flex;
  justify-content: center;
  overflow: hidden;
}

#streamDeckImg {
  width: 45%;
  height: auto;
}

#keyboardImg {
  width: 45%;
  height: auto;
}

#details {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

#phashImg {
  width: 80%;
}

#link {
  width: 100%;
}

#detailsContent {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

#detailsContent p {
  padding-left: 25px;
  padding-right: 25px;
}

#detailsContent h3 {
  width: 100%;
  align-self: flex-start;
  text-align: center;
  padding-left: 25px;
}

@media (max-width: 900px) {
  #detailsContent {
    flex-direction: column;
  }

  #detailsContent h3 {
    padding-left: 0;
  }
}
